import React, { useRef } from "react";
import "./design.css"

const Newsitem = (props) => {

  let { title, description, imageUrl, newsUrl, author, publishedAt } = props;
  return (
    <div className="card c_design">
      <img src={!imageUrl ? "./images/breakingnews.jpg" : imageUrl} className="card-img-top" alt="..." />
      <div className="card-body">
        <h5 className="card-title">{title}...</h5>
        <p className="card-text">{description}...</p>
        <p className="card-text">by {author ? author : "Unknown"}</p>
        <p> at {new Date(publishedAt).toGMTString()}</p>
        <a href={newsUrl} className="btn btn-sm btn-primary">
          Read More
        </a>
      </div>
    </div>
  );

}

export default Newsitem;

