import React, { useEffect, useRef, useState } from "react";
import Newsitem from "./Newsitem";
import Spinner from "./Spinner";
import PropTypes from "prop-types";
import InfiniteScroll from "react-infinite-scroll-component";
import Country from "./Country";
import "./style.css"



const News = (props) => {

  const [articles, setarticles] = useState([]);
  const [loading, setloading] = useState(false);
  const [totalResults, settotalResults] = useState(0);
  const [infiniteScroll, setinfiniteScroll] = useState(false);


  const Page = useRef("1")


  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  const handleChange = async () => {
    // props.setProgress(10);
    const url = `https://newsapi.org/v2/top-headlines?country=${countryRef.current.value}&&category=${props.category}&apiKey=e58983bbfe0e41f5877b091913836973&page=${Page.current}&pageSize=${props.pageSize}`;
    setloading(true);
    let data = await fetch(url);
    // props.setProgress(30);
    let parsedData = await data.json();
    // props.setProgress(70);
    // console.log(parsedData);
    // console.log(page)
    setarticles(parsedData.articles);
    settotalResults(parsedData.totalResults);

    setloading(false);
    // props.setProgress(100);

  }

  useEffect(() => {
    document.title = `${capitalizeFirstLetter(props.category)} - NewsMonkey`;
    handleChange();

  }, [])


  const handlePrevClick = async () => {
    Page.current--;
    handleChange();

  };

  const handleNextClick = async () => {
    // this.setState({ page: page + 1 });
    // setpage(page + 1);
    Page.current++;
    handleChange();
  };

  const fetchMoreData = async () => {
    const url = `https://newsapi.org/v2/top-headlines?country=${countryRef.current.value}&&category=${props.category}&apiKey=e58983bbfe0e41f5877b091913836973&page=${Page.current}&pageSize=${props.pageSize}`;
    Page.current++;
    let data = await fetch(url);
    let parsedData = await data.json()
    setarticles(articles.concat(parsedData.articles))
    settotalResults(parsedData.totalResults)
  };


  // const [country, setcountry] = useState("in")

  // const handlingChange = async (e) => {
  //   setcountry(e.target.value);
  //   handleChange();
  //   console.log(country);

  // }
  let countryRef = useRef(null);

  const countryOnchange = () => {
    console.log(countryRef.current.value);
    handleChange();
  }


  // console.log(check);
  const toogleInfiniteScroll = () => {
    if (infiniteScroll === true) {
      setinfiniteScroll(false)
    } else {
      setinfiniteScroll(true)
    }
  }

  // const pageSize = useRef("1");


  return (
    <>


      <div className="form-check form-switch infinitemode">
        <input
          className="form-check-input"
          type="checkbox"
          id="flexSwitchCheckDefault"
          onClick={toogleInfiniteScroll}
        />
        <label
          className="form-check-label text-dark"
          htmlFor="flexSwitchCheckDefault"
        >
          {infiniteScroll ? "Disable Infinite Mode" : "Enable Infinite Mode"}
        </label>
      </div>
      <Country llref={countryRef} countryOnchange={countryOnchange} />




      <div className="container my-3">
        <h1 className="text-center" style={{ margin: '35px 0px', marginTop: '50px' }}>NewsApp - Top {capitalizeFirstLetter(props.category)} Headlines</h1>
        {loading && <Spinner />}

        {!infiniteScroll &&
          <>
            <div className="row" style={{ margin: "1rem" }}>
              {!loading &&
                articles.map((element) => {
                  return (
                    <div className="col-md-4" key={element.url}>
                      <Newsitem
                        title={element.title ? element.title : ""}
                        description={element.description ? element.description : ""}
                        imageUrl={element.urlToImage}
                        newsUrl={element.url}
                        author={element.author}
                        publishedAt={element.publishedAt}
                        style={{ margin: "1rem, 0rem" }}
                      />
                    </div>
                  );
                })}
            </div>
            <div className="container d-flex justify-content-between">
              <button disabled={Page.current <= 1} type="button" className="btn btn-dark" onClick={handlePrevClick}>
                &larr; Previous
              </button>
              <p >{Page.current} / {Math.ceil(totalResults / props.pageSize)}</p>


              <button type="button" className="btn btn-dark" onClick={handleNextClick} disabled={Page.current >= Math.ceil(totalResults / props.pageSize)}>
                Next &rarr;
              </button>
            </div>
          </>
        }


        {infiniteScroll && <>
          <>
            <InfiniteScroll
              dataLength={articles.length}
              next={fetchMoreData}
              hasMore={articles.length !== totalResults}
              loader={<Spinner />}
            >
              <div className="container">

                <div className="row">
                  {articles.map((element) => {
                    return <div className="col-md-4" key={element.id}>
                      <Newsitem title={element.title ? element.title : ""}
                        description={element.description ? element.description : ""}
                        imageUrl={element.urlToImage}
                        newsUrl={element.url}
                        author={element.author}
                        publishedAt={element.publishedAt} />
                    </div>
                  })}
                </div>
              </div>
            </InfiniteScroll></>
        </>}



      </div >

      {/* 
      This is the main block

      <div className="row" style={{ margin: "1rem" }}>
          {!loading &&
            articles.map((element) => {
              return (
                <div className="col-md-4" key={element.url}>
                  <Newsitem
                    title={element.title ? element.title : ""}
                    description={element.description ? element.description : ""}
                    imageUrl={element.urlToImage}
                    newsUrl={element.url}
                    author={element.author}
                    publishedAt={element.publishedAt}
                    style={{ margin: "1rem, 0rem" }}
                  />
                </div>
              );
            })}
        </div>
        <div className="container d-flex justify-content-between">
          <button disabled={Page.current <= 1} type="button" className="btn btn-dark" onClick={handlePrevClick}>
            &larr; Previous
          </button>
          <p >1</p>


          <button type="button" className="btn btn-dark" onClick={handleNextClick} disabled={Page.current >= Math.ceil(totalResults / props.pageSize)}>
            Next &rarr;
          </button>
        </div> */}
    </>
  );

}


News.defaultProps = {
  country: "in",
  pageSize: "6",
  category: "general",
  // page: " 1",
};

News.propType = {
  country: PropTypes.string,
  pageSize: PropTypes.number,
  category: PropTypes.string,
  // page: PropTypes.number,
};


export default News;

